<template>
	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		<el-form-item label="手机号码" prop="telphone">
			<el-input v-model="ruleForm.telphone"></el-input>
		</el-form-item>

		<el-form-item label="密码" prop="password">
			<el-input v-model="ruleForm.password"></el-input>
		</el-form-item>

		<el-form-item label="确认密码" prop="repassword">
			<el-input v-model="ruleForm.repassword"></el-input>
		</el-form-item>

		<el-form-item label="所属单位" prop="department">
			<el-select v-model="ruleForm.department" placeholder="请选择">
				<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
			</el-select>
		</el-form-item>
	</el-form>
</template>

<script>
export default {
	data() {
		return {
			ruleForm: {
				telphone: '',
				password: '',
				repassword: ''
			},
			rules: {
				telphone: [
				{ required: true, message: '请输入手机号码', trigger: 'blur' },
				{ min: 11, max: 11, message: '长度为11个字符', trigger: 'blur' }
				],
				password: [
				{ required: true, message: '请输入密码', trigger: 'blur' },
				{ min: 6, max: 12, message: '长度为6-12个字符', trigger: 'blur' }
				],
				repassword: [
				{ required: true, message: '请输入密码', trigger: 'blur' },
				{ min: 6, max: 12, message: '长度为6-12个字符', trigger: 'blur' }
				],
				department: [
				{ required: true, message: '请选择单位', trigger: 'blur' }
				]
			},
			options: []
		}
	},
	mounted() {
		this.$http.get('/department')
		.then((response) => {
			this.options = response.data;
			console.log(response.data);
			console.log(response);
		})
		.catch((err) => {
			console.log('axios get请求失败');
		});


	}
}
</script>


